.nav {
  display: flex;
  width: 68.6%;
  justify-content: space-between;

  @media (max-width: $tablet-width) {
    width: 79.5%;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
  }
}

.nav__list {
  @include list-reset;
  display: flex;
  margin-top: 12px;

  @media (max-width: $tablet-width) {
    margin-right: 20px;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: auto;
  }
}

.nav__item {
  @include font-social-foot;
  margin-left: 40px;

  @media (max-width: 890px) {
    margin-left: 20px;
  }


  @media (max-width: $tablet-width) {
    margin-left: 25px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 16px;
    margin-right: 10%;
    width: 45%;
    order: 10;
  }
}


.nav__logo {
  margin-right: 24%;
  margin-top: 10px;
  margin-left: 5px;

  @media (max-width: $tablet-width) {
    margin-right: 9%;
    margin-left: 2px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 13px;
    margin-top: 2px;
    margin-left: 5px;
  }
}


.nav__item--promo {
  @media (max-width: $mobile-width) {
    order: 2;
    margin-top: -75px;
    margin-right: 0;
    margin-left: 120px;
  }
}

.nav__item--services {
  @media (max-width: $mobile-width) {
    order: 1;
    margin-top: 0;
  }
}

.nav__item--trainers {
  @media (max-width: $mobile-width) {
    order: 4;
    margin-top: -40px;
    margin-right: 0;
    margin-left: 120px;
  }
}

.nav__item--abon {
  @media (max-width: $mobile-width) {
    order: 3;
    margin-top: 10%;
  }
}

.nav__item--tab {
  @media (max-width: $mobile-width) {
    order: 5;
    margin-top: -10px;
    margin-left: 25%;
    margin-right: 0;
  }
}

.nav__link:hover,
.nav__link:focus {
  box-shadow: 0 0 0 4px $white;
}

.nav__link:active {
  opacity: 0.4;
}
